<template>
	<view>
		<!-- login view html start -->
		<view>
			<view>
				<view class="header"><image src="/static/images/photo.png"></image></view>
				<view class="content">
					<view>申请获取以下权限</view>
					<text>获得你的公开信息(昵称，头像、地区等)</text>
				</view>
				<button class="bottom" type="primary" open-type="getUserInfo" withCredentials="true" lang="zh_CN" @getuserinfo="wxGetUserInfo">授权登录</button>
			</view>
		</view>
		<!-- login view html end -->
	</view>
</template> 
 
<script>
export default {
	data() {
		return {
			appid: 'wx697aacdfea834ee8',
			secret: '2e68fe5bf3f4869cf539c0f7a28586e2', 
			code: '',
			sessionKey: '',
			openId: '',
			userInfo: {
				avatarUrl: '',
				city: '',
				country: '',
				gender: 1,
				language: '',
				phone: '',
				nickName: ''
			},
			user:[],
			pageOption: {}
		};
	},
	methods: {
		// 第一授权获取用户信息 ===》按钮触发
		wxGetUserInfo() {
			let _self = this;
			console.log(111);
			// 1.获取用户的信息
			uni.getUserInfo({
				provider: 'weixin',
				success: ( infoRes ) => {
					console.log( infoRes )
					_self.userInfo = infoRes.userInfo
					// 2.提交数据到后台、写入数据库
					uni.request({
						url: _self.apiServer + '/index.php/api/Wxxcx/get_userinfo',
						data: {
							openid: _self.openId,
							avatarUrl: _self.userInfo.avatarUrl,
							city: _self.userInfo.city,
							country: _self.userInfo.country,
							gender: _self.userInfo.gender,
							language: _self.userInfo.language,
							nickName: _self.userInfo.nickName
						},
						method: 'POST',
						success: res => {
							if( res.data.code != 0 )
							{
								uni.showToast({ title: res.data.msg, icon: 'none' });
								return false;
							}
							// 用户信息写入缓存
							uni.showToast({title: '登录成功'})
					        let userlist = {
							user_id:res.data.id,
						    user_nm: res.data.wx_name,
						    openid: res.data.openid,
						    user_img: res.data.img_url ,
						    phone: res.data.phone 
						  }				
										
						uni.setStorageSync( 'userlist', userlist );
							// 然后跳回原页面
							console.log( _self.pageOptio);
							if( _self.pageOption.backtype == 1 )
							{
								uni.redirectTo({ url: _self.pageOption.backpage })
							}else{
							uni.redirectTo({  
									url: _self.pageOption.backpage,
								})
							}
						},
						fail: () => {
							uni.showToast({ title: '用户信息操作失败', icon: 'none' });
						}
					});
				}, 
				fail: () => {
					uni.showToast({ title: '获取用户信息失败', icon: 'none' });
				}
			});
			return false
		},
		// 登录
		login() {
			let _self = this;

			// 0. 显示加载的效果
			uni.showLoading({
				title: '登录中...'
			});
			
			//return 111;

			// 1. wx 获取登录用户 code
			uni.login({ 
				provider: 'weixin',
				success: loginRes => {
					console.log(loginRes);
					_self.code = loginRes.code;
					// 2. 将用户登录code传递到后台置换用户SessionKey、OpenId等信息
					uni.request({
						url:
							'https://ccc.wangwanglive.com/index.php/api/Wxxcx/get_open?code=' +
							_self.code +
							'&grant_type=authorization_code',
						success: codeRes => {
							console.log(codeRes);
							_self.openId = codeRes.data.openid;
							_self.sessionKey = codeRes.data.session_key;
							// 3.通过 openId 判断用户是否授权
		
					// 隐藏loading
					uni.hideLoading();
					// 还没授权登录、请先授权然后登录 
					if (codeRes.data.code == 1) {
						// 提示消息、让用户授权
						uni.showToast({ title: codeRes.data.msg, icon: 'none' });
					}
					// 已经授权了、查询到用户的数据了
					if (codeRes.data.code == 0) {
						// 用户信息写入缓存
						uni.showToast({title: '登录成功'})
			   let userlist = {
						  user_id:codeRes.data.memberid,
						  user_nm: codeRes.data.wx_name,
						  openid: codeRes.data.openid,
						  user_img: codeRes.data.img_url ,
						  phone:codeRes.data.phone
						}               
										
										
						uni.setStorageSync( 'userlist', userlist );
						// uni.setStorageSync( 'user_nm', codeRes.data.wx_name );
						// uni.setStorageSync( 'openid', codeRes.data.openid);
						// uni.setStorageSync( 'user_img', codeRes.data.img_url );
						// 然后跳回原页面
					
						if( _self.pageOption.backtype == 1 )
						{
							uni.redirectTo({ url: _self.pageOption.backpage })
						}else{
								//console.log(_self.pageOption.backpage);
							uni.redirectTo({ url: _self.pageOption.backpage })
						}
					}
							
							
							
						},
						fail: () => {
							uni.showToast({ title: '获取 SesssionKey OpenId 失败', icon: 'none' });
							return false;
						}
					});
				},
				fail: () => {
					uni.showToast({ title: '获取 code 失败', icon: 'none' });
					return false;
				}
			});
			return false;
		}
	},
	onLoad( options ) {
		// 接收跳转的参数
		this.pageOption = options
		//默认加载
		this.login();
	}
};
</script>

